body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



nav {
  padding: 0 1.5rem;
}

.content{
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 1rem;
}

.container-spiner{
  margin-top: 5rem;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  
}

.movies{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  position: relative;
}

.movie-item{
  height: 400px;
}

.search-btn{
  position: absolute;
  top: 0;
  right: 0.5rem;
}

.filter-radio-btn{
  margin-right: 2rem;
}

.movie-descr-wrapper{
    background-color: rgb(249, 240, 170);
    width: 700px;
    z-index:  1000;
    padding: 10px 25px 25px 25px ;
    box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    left: 50%;
    top: 50%; 
    margin-left: -350px;
    margin-top: -300px;
    position: fixed;
    border: solid 3px black;
}



.title{
  text-align: center;
  font-weight: 600;
  font-size: 35px;
}

 
p{
  font-size: 15px;
  display: block;
}

.material-icons{
  margin-left: 95%;
  cursor: pointer;
  margin-top: 10px;
}

.descr{
display: inline-block;
}

.descr-wrapper{
  display: flex
}
.text{
  margin-left: 25px;
}


.load{
  display: block;
  margin: 0 auto;
  width: 150px;
  height: 45px;
  border-radius: 5px
}


